.spinner {
  width: 100%;
  height: 100%;
  z-index: 99998;
  background-color: rgba(0, 0, 0, .33);
  transition: background-color 1s;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.dot-flashing {
  width: 10px;
  height: 10px;
  color: #fff;
  z-index: 99999;
  border-radius: 100%;
  animation: dot-flashing 1s linear .5s infinite alternate;
  position: absolute;
  top: 50%;
  right: 50%;
}

.dot-flashing:before, .dot-flashing:after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
}

.dot-flashing:before {
  width: 10px;
  height: 10px;
  color: #fff;
  background-color: #fff;
  border-radius: 100%;
  animation: dot-flashing 1s infinite alternate;
  left: -15px;
}

.dot-flashing:after {
  width: 10px;
  height: 10px;
  color: #fff;
  background-color: #fff;
  border-radius: 100%;
  animation: dot-flashing 1s 1s infinite alternate;
  left: 15px;
}

@keyframes dot-flashing {
  0% {
    background-color: #ff001b;
  }

  50%, 100% {
    background-color: #fff;
  }
}

